.dialogs {
    display: grid;
    grid-template-columns: 3fr 7fr;
    line-height: 1;
    min-width: 150px;
}

.dialog img {
    width: 50px;
    height: 50px;
    border-radius: 25px;
    vertical-align: middle;
    margin-right: 15px;
}

.dialogItems {
    padding-right: 20px;
}

.dialog a {
    width: 80%;
    display: inline-block;
    padding: 10px 10px;
    min-width: 159px;
}

.dialog a:hover {
    background-color: rgba(231, 233, 234, 0.1);
    border-radius: 50px;
}

.dialog:first-child {
    padding-top: 5px;
}
.messages {
    padding: 10px 0px 15px;
}
.messageItem {
    padding: 7px 0;
}

.messages textarea {
    margin: 30px 40px 15px 0;
    vertical-align: bottom;
}

.messages button {
    vertical-align: bottom;
    margin: 30px 0 15px 0;
    width: 100px;
    height: 36px;
}